@use "../abstract-index" as *;

.aboutus{
    display: none;
}

.banner{
    min-height:fit-content;
    display: flex;
    flex-direction: column;
    gap: 18px;
    width: 320px;
    height: fit-content;
    margin: 0 auto;
    padding-bottom: 30px;  


    &__info-headeing{
        padding-top: 20px;
        font-size: 30px;
        font-weight: 700;
        text-align: center;

    }
    &__info-mini-heading{
        text-align: center;
        font-size: 20px;
        font-weight: 700;
    }   
    &__info-list{
        padding-left: 10px;
        line-height: 25px;
        font-weight: 200px;
    }
    &__info-list > li{
        list-style-type: circle;
    }

    

    
   
}


@include for-desktop{

    .banner{
        display: none;
    }
}

@include for-desktop{
    .background__container{
        display: none;
        
    }

    .aboutus{
        display: block;
        width: 100%;
        min-height: 550px;
        background-color:#212026;
    
    
        &__info{  
            margin: 0 auto;
            width: 1100px;
            height: 100%;
            position: relative;
            
            
    
      }
      &__wrapper{
        width: 550px;
        display: flex;       
        flex-direction: column;
      }
      &__heading{
        margin-top: 70px;
        margin-bottom: 30px;
        font-size: 34px;
        font-weight: 600;
      }
      &__button-wrapper{
        display: flex;
        gap: 40px;
        align-items: center;
        margin-top: 35px;
      }
      &__button{
        font-size: 16px;
        font-weight: 600;
        width:180px;
        height: 52px;
        border-radius: 4px;
    
    
      }
      &__list{
        line-height: 23px;
        margin-top: 25px;
        margin-left: 14px;
    
        
      }
      &__list > li{
        list-style: circle;
        
      }
    
        &__truck{
            height:550px;
            overflow: hidden;
            position: absolute;
      top: 0;
      left: 50%;
      
      }
      }
}

