@use "../abstract-index" as *;
.about-us{
    width: 320px;
    margin: 0 auto;
    &__heading{
        padding-top: 20px;
        font-size: $mobile-fonts-size;
        font-weight: 700;
        text-align: center;
    }
    &__list{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        padding-bottom: 50px;
    }
    &__item{
        margin-top: 45px;
        background-color: #761A1F;
        position: relative;
        width: 320px;
        height: fit-content;
        border-radius: 20px;
        display: flex;
        flex-direction: column;

        &--about-us{
            width: 320px;
            height: fit-content;
            
        }
    }
    &__item-header{
        text-align: center;
        font-size: 20px;
        font-weight: 700;
        margin-top: 30px;
        margin-left: 20px;
        margin-right: 20px;
  
    }
    &__item-text{

        line-height: 25px;
        font-weight: 200px;
        padding-top: 20px;
        padding-left: 20px;
        padding-right: 15px;
        padding-bottom: 30px;
  
    }
    &__item-text--desktop{
        display: none;

    }
    &__item-text--mobil{
        display: block;
    }
    &__item-icon{
        position: absolute;
        bottom: -50px;
        right: -50px;
  
    }

}




@include for-desktop{

    .about-us{
        width: 1082px;
        margin: 0 auto;
    &__heading{
        font-weight: 700;
        font-size: 38px;
        margin-top: 50px;
        margin-bottom: 10px;
        text-align: center;
    }
    &__list{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        padding-bottom: 50px;
    }
    &__item{
        margin-top: 30px;
        background-color: #761A1F;
        position: relative;
        width: 525px;
        height: 290px;
        border-radius: 20px;
        display: flex;
        flex-direction: column;

        &--about-us{
            width: 100%;
            min-height: 300px;
            
        }
    }
    &__item-text--desktop{
        display: block;

    }
    &__item-text--mobil{
        display: none;
    }
    &__item-header{
        font-size: 24px;
        font-weight: 700;
        margin-left: 28px;
        margin-top: 30px;
  
    }
    &__item-text{

        line-height: 25px;
        font-weight: 200px;
        padding-top: 20px;
        padding-left: 36px;
        padding-right: 36px;
  
    }
    &__item-icon{
        position: absolute;
        bottom: -30px;
        right: -30px;
  
    }
};
}


