@use "../abstract-index" as *;
.header-d{
    display: none;
}
.header{
    width: 340px;
    margin: 0 auto;


    
// logo
    &__logo-wrapper{
        display: flex;
        align-items: center;
        justify-content: space-between;       
        height: 70px;
        background-color: $red_collour;

    }
    &__logo-img{
        margin-top: 2px;

    }



// burger menu button
    &__burger-btn{
        display: block;
        margin-left: auto;
        width: 50px;
        height: 50px;
        left: 0;
        top: 0;
        z-index: 1;
        border: none;
        border-radius: 5px;
        background-color: rgba(255, 255, 255, 0);
        position: relative;
 
    }
    &__burger-btn span{
        position: absolute;
        width: 30px;
        height: 3px;
        background-color: rgb(255, 255, 255);
        left: 5px;
    }
    &__burger-btn span:nth-child(1){
        transform: translateY(-11px);
    }
    &__burger-btn span:nth-child(2){
        transform: translateY(-1px);
    }
    &__burger-btn span:nth-child(3){
        transform: translateY(9px);
        
    }

// mobile menu
    &__nav{
        top: -100%;
        left: 0;
        width: 100%;
        height: fit-content;
        background-color: rgba(0, 0, 0, 0.944);
        transition: left 0.3s ease 0s;
        padding: 40px; 
        padding-top: 50px;
        position: absolute;
        transition: top 0.3s ease 0s;
        
    }
    &__nav--active{
        top:0;
    }
    &__nav-list{
        display: flex;
        flex-direction: column;
        align-items: center;

    }
    &__nav-item{
        text-align: end;
        width: 320px;
        font-size: 20px;
        padding: 20px ;

    }

}


@include for-desktop{

    .header{
        display: none;
        
    }
    .header-d{
        display: block;
        width: 100%;
        background-color:#212026;
     
         &__wrapper{
             width: 1100px;
             margin: 0 auto;
             position: relative;
         }
    
         &__contact-list{
             display: flex;
             justify-content: space-between;
             align-items: center;
             min-height: 60px;
             border-bottom: 1px solid #33333331;
         }
         &__contact-item{
             display: flex;
             align-items: center;
             }
         
         &__contact-item-icon{   
              margin-right: 10px;
         }
         &__navigation{
             width: 100%;
             background-color: #a8262f;
            
         }
         &__navigation-wrapper{
             width: 1100px;
             margin: 0 auto;
             display: flex; 
             justify-content: space-between;
             align-items: center;
             min-height: 80px;
             
         }
         &__navigation-logo{
             display: flex;
             justify-content: center;
             align-items: center;
         }
         &__navigation-list{
             display: flex;
             gap: 46px;
     
     
         }
       
     
     }

}



