@use "../abstract-index" as *;
.location{
    width: 320px;
    min-height: 480px;
    margin: 0 auto;
    &__adress-wrapper{
        height:120px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-self: center;
    }
  
   
    &__adress-head{
        text-align: center;
        font-size: 20px;
        font-weight: 700;
    }
    &__adress{
        color: #979797;
        font-size: 15px;

    }
    &__map{
        width: 320px;
        height: 320px;
        margin: 0 auto;
        overflow: hidden;
    }
}


@include for-desktop{

    .location{
        width: 100%;
        min-height: 450px;
        background-color:#212026;
    
    
        &__wrapper{  
            margin: 0 auto;
            width: 1100px;
            display: flex;
            justify-content: space-between;
            align-items: center;
    
        }
        &__adress-wrapper{
            padding-left: 50px;
        }
        &__adress-head{
            font-weight: 700;
            font-size: 38px;
            margin-bottom: 10px;
        }
        &__adress{
            color: #979797;
            font-size: 15px;
    
        }
        &__map{
            overflow: unset;
        }
    }
    
}




