@use "../abstract-index" as *;
.contact{
    width: 320px;
    margin: 0 auto;
    &__headign{
        text-align: center;
    font-size: 20px;
    font-weight: 700;
        margin-top: 50px;
        margin-bottom: 50px;
        text-align: center;
    }

    &__list{
        margin-top: 30px;
        margin-bottom: 30px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        height: 360px;
    }
    &__item{
        display: flex;
        align-items: center;
        justify-content: center;

        background-color: #761A1F;
        border-radius: 20px;
        width: 150px;
        height: 100px;
        align-self: center;
    }
    &__item:last-child{
        width: 100%;
        

    }
    &__item > a{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__item > a > p{
        margin-top: 15px;
    }
    
}


@include for-desktop{
    .contact{
    width: 1150px;
    margin: 0 auto;
    min-height: 290px;

    &__headign{
        font-weight: 700;
        font-size: 38px;
        margin-top: 50px;
        margin-bottom: 50px;
        text-align: center;
    }

    &__list{
        justify-content: space-around;
        display: flex;
        margin-top: 30px;
        display: flex;
        flex-wrap: nowrap;
        height: auto;

    }
    &__item{
        background-color: #761A1F;
        border-radius: 20px;
        width: 150px;
        height: 100px;
    
    }
    &__item:last-child{
        width: 150px;
        

    }
    &__item > a{
        display: flex;
        flex-direction: column;
        align-items: center;
    }


    &__item > a > p{
        margin-top: 15px;
    }
}
}