@use "../abstract-index" as *;
@media (width<=600px) {
    .footer{
        width: 340px;
        margin: 0 auto;
        display: flex;
        justify-content: space-evenly;
        
    

    
    }
    

}
.footer{
    width: 340px;
    margin: 0 auto;
    display: flex;


    &__item{
        margin-top: 25px;
        margin-bottom: 25px;
        width: 155px;

    }


    &__item > ul > li{

        margin-left: 4px;
        margin-top: 12px;
    }
    &__item > ul > li > a{
        display: flex;
        gap: 1px;
        align-items: center;
    }
    &__navigation-headign{
        font-size: 15px;
        font-weight: 400;
        margin-bottom: 15px;
        
    }

}


@include for-desktop{

    .footer{
        width: 1150px;
        display: flex;
        justify-content: space-evenly;
        align-items: start;
        min-height: 250px;
        width: 1100px;
        margin: 0 auto;
        &__item{
            margin-top: 30px;
    
        }
        &__mobile{
            width: 480px;
            justify-content: space-between;
            display: flex;
        }
        &__item > ul > li{
            margin-left: 4px;
            margin-top: 12px;
        }
        &__item > ul > li > a{
            display: flex;
            gap: 1px;
            align-items: center;
        }
        &__navigation-headign{
            font-size: 20px;
            font-weight: 400;
            margin-bottom: 15px;
            
        }
    }
}